* {
    font-family: "Open Sans";
    font-weight: 400;
    scroll-behavior: smooth;
}

body {
    margin: 0;
}

.wrapper {
    max-width: 1170px;
    margin: 0 auto;
}

.sing-up {
    width: 210px;
    height: 38px;
    border-radius: 4px;
    background-color: #ef5b4c;
    border: none;
    outline: none;
    font-size: 16px;
    color: #fefefe;
    cursor: pointer;
    &:hover {
        background-color: #d24335;
    }
    &:disabled {
        width: 210px;
        height: 40px;
        border-radius: 4px;
        color: #8d8c8c;
        filter: drop-shadow(0px 7px 4.5px rgba(0,0,0,0.11));
        background-color: #d7d7d7;
    }
}